.contacto {
	fieldset {
		legend {
			// color: $oscuro;
			@include tipografia2($tipo2, 2.2rem, $oscuro, 400, .1rem);
		}
		label {
			// color: $oscuro;
			@include tipografia2($tipo1, 1.8rem, $oscuro, 100, .1rem);
		}
		input, textarea, select {
			@include tipografia2($tipo3, 1.8rem, $oscuro, 100, .1rem);
			background-color: white;
			border: 2px solid $grisOscuro;
			padding: 1rem;
			display: block;
			width: 100%;
			border: 1px solid #333;
			border-radius: 1rem;
			margin-bottom: 2rem;
		}
		p {
			@include tipografia2($tipo1, 1.5rem, $oscuro, 100, .1rem);
		}
		.forma-contacto {
			max-width: 30rem;
			display: flex;
			justify-content: space-between;
			align-items: center;
			flex-direction: column;
			margin: auto;

			@include media550 {
				flex-direction: row;
				margin: 0;
			}
			input {
				margin: 0;
				width: auto;
			}
		}
	}
	input {
		background-color: #71b100;
		color: #fff;
		font-weight: 700;
		text-decoration: none;
		padding: 1rem 3rem;
		text-align: center;
		margin-top: 2.5rem;
		display: inline-block;
		border: none;
	}
}

.dark-mode {
	fieldset {
		legend, label, p {
			color: $grisaceo;
		}
		input, textarea, select{
			color: $grisaceo;
			background-color: $grisOscuro;
			border: 2px solid $grisaceo;
		}
	}
}