.seccion {
	.titulo {
		@include tipografia2($tipo1, 3.3rem, $negro, 100, .25rem);
	}

	.iconos-nosotros {
		display: flex;
		flex-direction: column;
		grid-gap: 2rem;

		@include media768 {
			display: grid;
			grid-template-columns: repeat(3, 1fr);
			// flex-direction: row;
		}
		.icono {
			img {
				width: auto;
				height: 10rem;
				justify-content: center;
				margin: auto;
			}
			h3 {
				@include tipografia2($tipo2, 3rem, $negro, 100, .22rem);
			}
			p {
				@include tipografia($tipo3, 2.4rem, $negro, 100);				
				margin: auto 10%;
				@include media768 {
					margin: auto;
				}
			}
		}
	}
}

.dark-mode {
	.seccion {
		.iconos-nosotros {
			.cambio {
				color: $grisaceo;
			}
		}
	}
	.cambio {
		color: $grisaceo;
	}
}