.anuncio {
	display: flex;
	flex-direction: column;
	justify-content: space-between;
	background-color: $huevo;
	border: 2px solid #333;
}

.resumen-propiedad {
	background-color: $huevo;
	border: 2px solid #333;
}

.contenido-anuncio, .resumen-propiedad {
	padding: 2rem;
 
	h3 {
		@include tipografia2($tipo2, 2.4rem, $negro, 100, .12rem);
		margin: 0;
		text-transform: capitalize;
	}
	p {
		@include tipografia($tipo3, 2.4rem, $negro, 100);				
		text-align: center;
		margin-bottom: 0;
	}
	.precio {
		@include tipografia2($tipo2, 2.8rem, $rojo, 100, .12rem);
		margin: 0;
	}

	.iconos-caracteristicas {
		display: flex;
		flex-direction: row;
		justify-content: space-evenly;
	    list-style: none;
	    padding: 0;
	    max-width: 50rem;
		margin: auto;
		li {
			display: flex;
			flex-direction: row;
			p {
				justify-content: center;
				margin: auto 1rem;
				@include tipografia($tipo3, 2.8rem, $negro, 100);
			}
		}
		.icono {
			// width: 100%;
		}
	}
	a {
		background-color: #e08709;
	    color: #fff;
		@include tipografia($tipo1, 2rem, white, 700);
	    text-decoration: none;
	    padding: 1rem 3rem;
	    text-align: center;
	    margin-top: 2.5rem;
	    display: block;
	    border: none;

	    &:hover {
			background-color: #BC6E00;
	    }
	}
}

.todas {
	display: flex;
	justify-content: flex-end;
	.boton-verde {
	    background-color: #71b100;
	    color: #fff;
		@include tipografia($tipo1, 2rem, white, 700);
	    text-decoration: none;
	    padding: 1rem 3rem;
	    text-align: center;
	    margin-top: 2.5rem;
	    display: inline-block;
	    border: none;

	    &:hover {
			background-color: #548006;
	    }
	}
}

.dark-mode {
	.anuncio, .resumen-propiedad {
		background-color: $grisOscuro;
		h3 {
			color: $grisaceo;
		}
		p {
			color: $grisaceo;
		}
		.precio {
			color: $verde;
		}
		li {
			p, .icono {
				filter: invert(100%);
			}
		}
	}
}