.contenido-nosotros {
	display: grid;
	flex-direction: column;

	@include media850 {
		grid-template-columns: repeat(2, 1fr);
		grid-gap: 2rem;
	}
	.texto-nosotros {
		blockquote {
			@include tipografia2($tipo2, 3rem, $oscuro, 400, .1rem);
		}
		p {
			@include tipografia2($tipo3, 2.2rem, $oscuro, 400, .1rem);
		}
	}
}

.dark-mode {
	.texto-nosotros {
		blockquote, p {
			color: $grisaceo;
		}
	}
}